@import 'font-face';

/* Typography
   ========================================================================== */

$font-family-sans-serif: 'Helvetica Neue', Helvetica, Arial, 'Rhythm Heiti', sans-serif;
$font-family-serif: 'Rhythm Italic Serif', 'Rhythm Songti', sans-serif;
$font-family-cursive: Georgia, 'Times New Roman', 'Rhythm Cursive', serif;
$font-family-monospace: Menlo, Courier, monospace, monospace, sans-serif;

$font-size-root: 15px;
$font-size-base: 1rem;

$font-size-large: 1.2rem;
$font-size-small: 0.8rem;

$font-size-h1: 1.8rem;
$font-size-h2: 1.6rem;
$font-size-h3: 1.4rem;
$font-size-h4: 1.2rem;
$font-size-h5: 1.2rem;
$font-size-h6: 1.2rem;

$line-height-heading: 1.6em;
$line-height-article: 1.6em;

$indent: 2em;

$vertical-space: 24px;

/* Color
   ========================================================================== */

$grey-base: #9e9e9e;
$grey-darker: #757575;
$grey-dark: #616161;
$grey-light: #bdbdbd;
$grey-lighter: #e0e0e0;
$grey-100: #f5f5f5;

$black-base: #000;
$black-dark: lighten($black-base, 13.5%); // #222
$black-light: lighten($black-base, 20%);   // #333

$red-base: #f44336;
$red-dark: #d32f2f;
$red-light: #e57373;

$blue-base: #2196f3;
$blue-dark: #1976d2;
$blue-light: #64b5f6;

/* Color Apply
   ========================================================================== */

$color-body: #fff;
$color-heading: #444;
$color-text: $black-light;

$color-link: $black-light;
$color-link-hover: $black-dark;
$color-link-active: $black-dark;
$color-link-visited: #9f9f9f;

$color-code: $red-light;
$color-code-border: $grey-lighter;
$color-code-bg: #fff;

$color-blockquote-border: $grey-lighter;

$color-table-border: $grey-lighter;
$color-table-th-bg: $grey-100;
$color-table-td-bg: #fff;

$color-hr: $grey-lighter;
$color-figcaption: $grey-base;

/* Space
   ========================================================================== */

$rhythm-headding-top: 1.33em;
$rhythm-headding-bottom: .44em;
$rhythm-text-bottom: 1em;

/* Support
   ========================================================================== */

$support-em: true;
