@import "rhythm/rhythm";

.navbar {
  height: 80px;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.15);
  line-height: 80px;
  letter-spacing: .125em;
  font-size: 24px;
  color: #40444f;
  text-align: center;

  a {
    color: #333;

    &:hover,
    &:active {
      color: #333;
    }
  }
}

article.rhythm {
  width: 720px;
  margin: 4em auto;

  .title {
    text-align: center;
    font-size: 24px;
    margin-bottom: 1em;
    font-family: serif;
    font-weight: bold;
    line-height: 1.6em;
  }
}

@media (max-width: 720px) {
  .navbar {
    font-size: 18px;  
  }

  article.rhythm {
    width: 100%;
    padding: 0 5%;
    margin: 2em auto;
  }
}
